body {
	background: #141414;
	color: #ffffff;
}
::-webkit-scrollbar{
    display: none;
}
.movie-app > .row {
	overflow-x: auto;
	flex-wrap: nowrap;
}

.image-container {
	position: relative;
	transition: transform 0.2s;
}

.image-container:hover {
    transition: 0.3s all ease;
    transform: scale(1.1);
    
  color: rgba(255, 255, 255, 1);
  box-shadow: 0 5px 15px rgba(241, 6, 57, 0.64);
}

.image-container:hover .overlay {
	opacity: 1;
}

.overlay {
	position: absolute;
	background: rgba(0, 0, 0, 0.8);
	width: 100%;
	transition: 0.5s ease;
	opacity: 0;
	bottom: 0;
	font-size: 20px;
	padding: 20px;
	text-align: center;
}
